function RenderSunburstChart(props) {
    console.log('RenderSunburstChart', props);
    if (!props.data) {
        throw new Error("data property not found. please pass data prop to the function.");
    }
    let data = props.data;
    let width = props.width;
    let targetId = props.targetId;
    let radius = props.width / 6;
    const format = d3.format(",d");
    const partition = (data) => {
        const root = d3
            .hierarchy(data)
            .sum((d) => d.value)
            .sort((a, b) => b.value - a.value);
        return d3.partition().size([2 * Math.PI, root.height + 1])(root);
    };
    const color = d3.scaleOrdinal(d3.quantize(d3.interpolateRainbow, data.children.length + 1)); // returns a function
    const arc = d3
        .arc()
        .startAngle((d) => d.x0)
        .endAngle((d) => d.x1)
        .padAngle((d) => Math.min((d.x1 - d.x0) / 2, 0.005))
        .padRadius(radius * 1.5)
        .innerRadius((d) => d.y0 * radius)
        .outerRadius((d) => Math.max(d.y0 * radius, d.y1 * radius - 1));
    const createChart = (data) => {
        const root = partition(data);
        root.each((d) => (d.current = d));
        const svg = d3
            .create("svg")
            .attr("viewBox", [0, 0, width, width])
            .style("fill", "black")
            .style("font", "10px sans-serif")
            .style("font-weight", "bold");
        const g = svg
            .append("g")
            .attr("transform", `translate(${width / 2},${width / 2})`);
        const path = g
            .append("g")
            .selectAll("path")
            .data(root.descendants().slice(1))
            .join("path")
            .attr("fill", (d) => {
            while (d.depth > 1)
                d = d.parent;
            return color(d.data.name);
        })
            .attr("fill-opacity", (d) => arcVisible(d.current) ? (d.children ? 0.6 : 0.4) : 0)
            .attr("pointer-events", (d) => (arcVisible(d.current) ? "auto" : "none"))
            .attr("d", (d) => arc(d.current));
        path
            .filter((d) => d.children)
            .style("cursor", "pointer")
            .on("click", clicked);
        path.append("title").text((d) => `${d
            .ancestors()
            .map((d) => d.data.name)
            .reverse()
            .join("/")}\n${format(d.value)}`);
        const label = g
            .append("g")
            .attr("pointer-events", "none")
            .attr("text-anchor", "middle")
            .style("user-select", "none")
            .selectAll("text")
            .data(root)
            .join("text")
            .attr("dy", "0.35em")
            .attr("fill-opacity", (d) => +labelVisible(d.current))
            .attr("transform", (d) => labelTransform(d.current))
            .text((d) => d.data.name);
        const parent = g
            .append("circle")
            .datum(root)
            .attr("r", radius)
            .attr("fill", "none")
            .attr("pointer-events", "all")
            .on("click", clicked);
        function clicked(event, p) {
            console.log(event, p);
            parent.datum(p.parent || root);
            root.each((d) => (d.target = {
                x0: Math.max(0, Math.min(1, (d.x0 - p.x0) / (p.x1 - p.x0))) *
                    2 *
                    Math.PI,
                x1: Math.max(0, Math.min(1, (d.x1 - p.x0) / (p.x1 - p.x0))) *
                    2 *
                    Math.PI,
                y0: Math.max(0, d.y0 - p.depth),
                y1: Math.max(0, d.y1 - p.depth),
            }));
            const t = g.transition().duration(750);
            // Transition the data on all arcs, even the ones that aren’t visible,
            // so that if this transition is interrupted, entering arcs will start
            // the next transition from the desired position.
            path
                .transition(t)
                .tween("data", (d) => {
                const i = d3.interpolate(d.current, d.target);
                return (t) => (d.current = i(t));
            })
                // .filter(function (d : any) {
                //   if(this){
                //     let th:any = this 
                //     return !!Number(th.getAttribute("fill-opacity"))
                //   }
                //   return arcVisible(d.target);
                // })
                .filter(function (d) {
                let th = this;
                return !!(+th.getAttribute("fill-opacity") || arcVisible(d.target));
            })
                .attr("fill-opacity", (d) => arcVisible(d.target) ? (d.children ? 0.6 : 0.4) : 0)
                .attr("pointer-events", (d) => (arcVisible(d.target) ? "auto" : "none"))
                .attrTween("d", (d) => () => {
                let a = arc(d.current);
                if (a) {
                    return a;
                }
                return "";
            });
            label
                // .filter(function (d : any) {
                //   if(this){
                //     let th:any = this 
                //     return !!Number(th.getAttribute("fill-opacity")) 
                //   }
                //   return labelVisible(d.target);
                // })
                .filter(function (d) {
                let th = this;
                return !!(+th.getAttribute("fill-opacity") || labelVisible(d.target));
            })
                .transition(t)
                .attr("fill-opacity", (d) => +labelVisible(d.target))
                .attrTween("transform", (d) => () => labelTransform(d.current));
        }
        function arcVisible(d) {
            return d.y1 <= 3 && d.y0 >= 1 && d.x1 > d.x0;
        }
        function labelVisible(d) {
            return d.y1 <= 3 && d.y0 >= 1 && (d.y1 - d.y0) * (d.x1 - d.x0) > 0.03;
        }
        function labelTransform(d) {
            const x = (((d.x0 + d.x1) / 2) * 180) / Math.PI;
            const y = ((d.y0 + d.y1) / 2) * radius;
            return `rotate(${x - 90}) translate(${y},0) rotate(${x < 180 ? 0 : 180})`;
        }
        return svg.node();
    };
    const renderChart = () => {
        // if target element is not present in dom, then create it.
        if (!document.getElementById(targetId)) {
            let div = document.createElement("div");
            div.setAttribute("id", targetId);
            document.appendChild(div);
        }
        const target = document.getElementById(targetId);
        if (target) {
            const div = document.createElement("div");
            div.style.width = width + 'px';
            // here we create the chart
            let svgNode = createChart(data);
            div.appendChild(svgNode);
            target.replaceChildren(div);
        }
    };
    renderChart();
}

const formatSunburstResponse = (data) => {
    let obj = {
        name: data.categoryName.substring(0, 11) + '..',
    };
    if (data.totalAmount) {
        obj.value = data.totalAmount;
    }
    if (data.subCategories && data.subCategories.length) {
        obj.children = [];
        data.subCategories.map((category) => {
            obj.children.push(formatSunburstResponse(category));
        });
    }
    return obj;
};

const fetchSunburstChartData = ({ endpoint, payload }) => {
    return new Promise((resolve, reject) => {
        axios.post(endpoint, payload).then(res => {
            console.log(res);
            res.data.categoryName = 'All Tnx';
            resolve({
                data: formatSunburstResponse(res.data)
                // data: dataFormatForChart
            });
        }).catch(err => {
            reject(err);
        });
    });
};

